&-alert {
  margin-bottom: 16px;

  &-links {
    margin-top: 14px;

    a {
      margin-bottom: 8px;

      &:not(:last-child) {
        margin-right: 16px;
      }
    }

    &_vertical {
      a:not(:last-child) {
        margin-right: 0;
        margin-bottom: 8px;
      }
    }

    &__label {
      margin-right: 8px;
    }
  }

  .ssp-player-markdown-editor {
    *:last-child {
      margin: 0;
    }
  }
}
